@import url(https://fonts.googleapis.com/css2?family=Sora&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	font-family: "Sora", sans-serif;
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

:root {
	--primary: #1888ff;
}

.btn {
	padding: 8px 20px;
	border-radius: 4px;
	outline: none;
	border: none;
	font-size: 18px;
	color: #fff;
	height: 100%;
	width: 100%;
	cursor: pointer;
	background-color: #1888ff;
	background-color: var(--primary);
}
.btn-mobile {
	padding: 2%;
	border-radius: 4px;
	outline: none;
	border: none;
	font-size: 18px;
	color: #fff;
	height: 100%;
	width: 100%;
	cursor: pointer;
	background-color: #1888ff;
	background-color: var(--primary);
}

.btn:hover {
	padding: 6px 18px;
	transition: all 0.3s ease-out;
	background-color: transparent;
	color: black;
	border-radius: 4px;
	border: 2px solid #1888ff;
	border: 2px solid var(--primary);
	transition: all 0.3s ease-out;
}

.navbar {
	height: 12vh;
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	font-size: 1.2rem;
	-webkit-flex-shrink: 1;
	        flex-shrink: 1;
	position: -webkit-sticky;
	background-color: white;
	top: 0;
	margin-bottom: 0.5rem;
	padding-top: 0.5rem;
}

.navbar-logo {
	color: #fff;
	justify-self: start;
	margin-left: 20px;
	cursor: pointer;
	text-decoration: none;
}

.fa-firstdraft {
	margin-left: 0.5rem;
}

.nav-menu {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	list-style: none;
	text-align: center;
	width: 100%;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	margin-right: 2rem;
	margin-left: 10%;
}
.nav-menu-mobile {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	width: 100%;
	background-color: rgb(202, 232, 250);
	height: 100%;
	list-style: none;
	text-align: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
}

.nav-item {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	height: 80%;
}

.nav-links {
	color: #242222;
	text-decoration: none;
	padding: 0.5rem 1rem;
}
.nav-links-footer {
	color: #242222;
	text-decoration: none;
}

.nav-links:hover {
	color: #1d3f6e;
	border-radius: 4px;
	transition: all 0.2s ease-out;
}
.nav-links-footer:hover {
	color: lightskyblue;
	transition: all 0.2s ease-out;
}

.nav-links-active  {
	color: lightskyblue;
	transition: all 0.2s ease-out;
}

.fa-bars {
	color: black;
}

.nav-links-mobile {
	display: none;
}

.menu-icon {
	display: none;
}

.dropdown-element {
	text-decoration: none;
	padding-left: 1rem;
	padding-right: 1rem;
	padding-bottom: 0.5rem;
	padding-top: 0.5rem;
	color: black;
	font-size: 14px;
}
.dropdown-element:hover {
	text-decoration: none;
	background-color: lightblue;
	cursor: pointer;
}
.menu-text-active {
	-webkit-align-items: center;
	        align-items: center;
	color: #1888ff;
	-webkit-justify-content: center;
	        justify-content: center;
	align-items: center;
}
.menu-text {
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	align-items: center;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	cursor: pointer;
}
.menu-text-active {
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
