}

.dropDownContainer {
	display: none;
}
.dropdownContainerActive {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	position: absolute;
	width: 12vw;
	background-color: white;
	border: 1px solid lightgrey;
	box-shadow: 1px 1px 1px lightblue;
	border-radius: 2px;
}

